import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import Highcharts from 'highcharts';
import Button from '@material-ui/core/Button';
import equal from 'fast-deep-equal';
import HighchartsReact from 'highcharts-react-official';
import { fetchVizHoursPerLabel } from '../../../../actions/api/viz';
import CircularProgress from '../../../lib/CircularProgress';
import language from '../../../../constants/language';

function getKeyByValue(object, value) {
  return Object.keys(object).find(key => object[key] === value);
}

class BoardHours extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: true,
    };
    this.setOptions.bind(this);
    this.buttonClick.bind(this);
  }

  componentWillMount() {
    this.setState({ loading: false });
  }

  componentDidUpdate(prevProps) {
    if (!equal(this.props.data, prevProps.data)) {
      const chartOptions = this.setOptions();
      Highcharts.chart('chart', chartOptions);
    }
    if (!equal(this.props.selectedLanguage, prevProps.selectedLanguage)) {
      const chartOptions = this.setOptions();
      Highcharts.chart('chart', chartOptions);
    }
  }

  setOptions() {
    const formattedData = this.formatData();
    const boardsObject = this.props.labels;
    const fetchBoardInfo = this.props.fetchVizHoursPerLabel;
    const [groupBy, startDate, endDate] = this.props.params;
    const dates = this.props.data.map(d => d.date);
    // eslint-disable-next-line prefer-destructuring
    const selectedLanguage = this.props.selectedLanguage;
    let newOptions;
    const chartOptions = {
      chart: {
        type: 'column',
        height: 350,
      },
      credits: false,
      title: {
        text: language[selectedLanguage].dynamicChart.boardReportedHours,
      },
      xAxis: {
        categories: this.props.data.map(d => d.date),
      },
      yAxis: {
        min: 0,
        title: {
          text: language[selectedLanguage].dynamicChart.totalHours,
        },
      },
      legend: {
        reversed: true,
      },
      series: formattedData,
      tooltip: {
        backgroundColor: '#616161',
        borderColor: '#616161',
        borderWidth: 1,
        style: {
          color: 'white',
          fontFamily: '"Roboto", "Helvetica", "Arial", sans-serif',
          textAlign: 'center',
        },
        formatter() {
          return `<tspan style="font-size: 10px">${this.x}<tspan> <br> <span style="color: ${this.color}">\u25CF</span> 
          <b>${this.series.name} </b>: <b>${this.y}</b> <br> <i>${language[selectedLanguage].dynamicChart.clickMessage}</i>`;
        },
      },
      plotOptions: {
        series: {
          stacking: 'normal',
          cursor: 'pointer',
          point: {
            events: {
              click() {
                const selectedBoardName = this.series.name;
                const selectedBoardId = getKeyByValue(
                  boardsObject,
                  selectedBoardName,
                );
                fetchBoardInfo(selectedBoardId, {
                  group_by: groupBy,
                  start_date: startDate,
                  end_date: endDate,
                }).then((response) => {
                  if (response.ok) {
                    const boardData = response.body.data;
                    const boardLabels = response.body.labels;
                    const newData = [];
                    boardLabels.forEach((label) => {
                      newData.push({
                        name: label,
                        data: boardData.map(row => row[label]),
                      });
                    });
                    newOptions = {
                      chart: {
                        type: 'column',
                        height: 350,
                      },
                      credits: false,
                      title: {
                        text: language[selectedLanguage].dynamicChart.labelReportedHours,
                      },
                      xAxis: {
                        categories: dates,
                      },
                      yAxis: {
                        min: 0,
                        title: {
                          text: language[selectedLanguage].dynamicChart.totalHours,
                        },
                      },
                      legend: {
                        reversed: true,
                      },
                      series: newData,
                      plotOptions: {
                        series: {
                          stacking: 'normal',
                        },
                      },
                      tooltip: {
                        backgroundColor: '#616161',
                        borderColor: '#616161',
                        borderWidth: 1,
                        style: {
                          color: 'white',
                          fontFamily: '"Roboto", "Helvetica", "Arial", sans-serif',
                        },
                      },
                    };
                    Highcharts.chart('chart', newOptions);
                  }
                });
              },
            },
          },
        },
      },
    };
    return chartOptions;
  }

  formatData() {
    const boardNames = Object.values(this.props.labels);
    const newData = [];
    boardNames.forEach((board) => {
      newData.push({
        name: board,
        data: this.props.data.map(row => row[board]),
      });
    });
    return newData;
  }

  buttonClick() {
    const chartOptions = this.setOptions();
    Highcharts.chart('chart', chartOptions);
  }

  render() {
    if (this.state.loading) return <CircularProgress />;
    const chartOptions = this.setOptions();
    return (
      <div>
        <div id="chart">
          <HighchartsReact highcharts={Highcharts} options={chartOptions} />
        </div>
        <Button
          variant="contained"
          id="hours-chart-reset-button"
          onClick={() => this.buttonClick()}
        >
          {language[this.props.selectedLanguage].dynamicChart.reset}
        </Button>
      </div>
    );
  }
}

BoardHours.propTypes = {
  data: PropTypes.array.isRequired,
  labels: PropTypes.array.isRequired,
  params: PropTypes.array.isRequired,
  fetchVizHoursPerLabel: PropTypes.func.isRequired,
  selectedLanguage: PropTypes.string.isRequired,
};

const mapStateToProps = (state) => {
  return {
    selectedLanguage: state.user.language,
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({ fetchVizHoursPerLabel }, dispatch);
};

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(BoardHours);
