import React, { Component } from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';

import Typography from '@material-ui/core/Typography';

import 'react-day-picker/lib/style.css';

import CircularProgress from '../../../lib/CircularProgress';
import { notifyError } from '../../../../actions/notification';

import {
  fetchVizHoursPerBoard,
  fetchVizHoursPerLabel,
} from '../../../../actions/api/viz';
import { changeReportHours } from '../../../../actions/project';

import DynamicChart from './DynamicHours';
import language from '../../../../constants/language';

/* eslint-disable react/no-unused-state */
const styles = {
  wrapper: {
    height: '400px',
  },
  modalContent: {
    width: '500px',
    height: '400px',
  },
  title: {
    fontSize: '28px',
    fontFamiliy:
      '-apple-system, ".SFNSText-Regular", "San Francisco", BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Fira Sans", "Droid Sans", "Helvetica Neue", "Lucida Grande", Arial, sans-serif',
    color: 'rgb(68, 68, 68)',
  },
  headerContainer: {
    margin: '30px',
    width: 'auto',
  },
  text: {
    color: '#2a3d41',
    fontSize: 16,
    fontWeight: 'bold',
    marginTop: '10px',
    marginBottom: '10px',
  },
  selectText: {
    color: '#2a3d41',
    fontSize: 14,
    fontWeight: 'normal',
    marginTop: '10px',
    marginBottom: '10px',
    marginLeft: '10px',
  },
  grid: {
    display: 'flex',
    width: '100%',
  },
  container: {
    height: '700px',
  },
  modalContainer: {
    height: '700px',
  },
  line: {
    width: 'auto',
    height: '1px',
    backgroundColor: '#CCCCCC',
  },
};

class HoursContainer extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: true,
      data: null,
      labels: null,
      total: null,
      selected: null,
      selectedIndex: null,
    };
    this.changeData = this.changeData.bind(this);
  }

  componentWillMount() {
    this.changeData(
      this.props.filterOption,
      this.props.startDate,
      this.props.endDate,
    );
  }

  changeData(filterOption, startDate, endDate) {
    this.props
      .fetchVizHoursPerBoard(this.props.projectId, {
        start_date: startDate,
        end_date: endDate,
        group_by: filterOption,
      })
      .then((response) => {
        if (response.ok) {
          const getValue = (v) => {
            const newV = v < 1 ? 0 : v;
            return newV;
          };

          let summedData = 0;
          let max = 0;
          let tmp = 0;
          const keys = Object.values(response.body.boards);
          response.body.data.forEach((elem) => {
            keys.forEach((key) => {
              summedData += getValue(elem[key]);
              tmp += getValue(elem[key]);
            });
            if (tmp > max) {
              max = tmp;
            }
            tmp = 0;
          });
          this.setState({
            loading: false,
            data: response.body.data,
            labels: response.body.boards,
            total: summedData,
          });
        }
      });
  }

  render() {
    if (this.state.loading) return <CircularProgress />;
    const {
      data, labels, total,
    } = this.state;
    const { classes } = this.props;
    return (
      <div className={classes.container}>
        <div className={classes.headerContainer}>
          <Typography className={classes.title}>
            {language[this.props.selectedLanguage].hoursIndex.totalHours}
          </Typography>
          <div className={classes.line} />
          <div className={classes.grid}>
            <div
              style={{
                display: 'flex',
                alignItems: 'center',
                marginLeft: '10px',
              }}
            >
              <Typography className={classes.text}>
                {language[this.props.selectedLanguage].hoursIndex.from}{' '}{this.props.startDate}
              </Typography>
            </div>
            <div
              style={{
                display: 'flex',
                alignItems: 'center',
                marginLeft: '10px',
              }}
            >
              <Typography className={classes.text}>
                {language[this.props.selectedLanguage].hoursIndex.to}{' '}{this.props.endDate}
              </Typography>
            </div>
          </div>
          <div
            style={{ display: 'flex', alignItems: 'center', margin: '10px' }}
          >
            <Typography className={classes.text}>
              {language[this.props.selectedLanguage].hoursIndex.filterBy}{' '}{this.props.filterOption}
            </Typography>
          </div>
          <div className={classes.line} />
          {total > 0 && (
            <div style={{ display: 'flex' }}>
              <Typography className={classes.text}>
                {language[this.props.selectedLanguage].hoursIndex.totalHours}
                &nbsp;
              </Typography>
              <Typography className={classes.text}>{Math.round(total * 100) / 100}</Typography>
            </div>
          )}
        </div>
        <div className={classes.wrapper}>
          {data.length > 0 && (
            <DynamicChart
              data={data}
              labels={labels}
              params={[this.props.filterOption, this.props.startDate, this.props.endDate]}
            />
          )}
          {!data.length && (
            <Typography style={{ textAlign: 'center' }}>
              {' '}
              {language[this.props.selectedLanguage].hoursIndex.noReported}{' '}
            </Typography>
          )}
        </div>
      </div>
    );
  }
}

HoursContainer.propTypes = {
  classes: PropTypes.object.isRequired,
  projectId: PropTypes.string.isRequired,
  fetchVizHoursPerBoard: PropTypes.func.isRequired,
  notifyError: PropTypes.func.isRequired,
  filterOption: PropTypes.string.isRequired,
  endDate: PropTypes.string.isRequired,
  startDate: PropTypes.string.isRequired,
  changeReportHours: PropTypes.func.isRequired,
  selectedLanguage: PropTypes.string.isRequired,
};

const mapStateToProps = (state) => {
  return {
    selectedLanguage: state.user.language,
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators(
    {
      fetchVizHoursPerBoard,
      fetchVizHoursPerLabel,
      changeReportHours,
      notifyError,
    },
    dispatch,
  );
};

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(withStyles(styles)(HoursContainer));
